import React, { useState } from "react";
import { Menu, Layout, theme, Modal, Input } from "antd";
import constants from "../constants";
import LoginForm from "../components/LoginForm";
import { NavLink } from "react-router-dom";

const { Header, Content, Footer } = Layout;
const items = [
  { key: 1, label: "Login" },
  { key: 2, label: "Register" },
];
const Landing = () => {
  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();

  const [isLoginModalOpen, setIsLoginModalOpen] = useState(false);
  const showLoginModal = (key) => {
    setIsLoginModalOpen(true);
  };
  const handleLoginCancel = () => {
    setIsLoginModalOpen(false);
  };
  function LoginModal() {
    return (
      <Modal
        title="Login"
        open={isLoginModalOpen}
        onCancel={handleLoginCancel}
        footer=""
        centered
      >
        <LoginForm />
      </Modal>
    );
  }

  const [isRegisterModalOpen, setIsRegisterModalOpen] = useState(false);
  const showRegisterModal = (key) => {
    setIsRegisterModalOpen(true);
  };
  const handleRegisterOk = () => {
    setIsRegisterModalOpen(false);
  };
  const handleRegisterCancel = () => {
    setIsRegisterModalOpen(false);
  };
  function RegisterModal() {
    return (
      <Modal
        title="Register"
        open={isRegisterModalOpen}
        onOk={handleRegisterOk}
        onCancel={handleRegisterCancel}
      >
        <Input placeholder="Enter Email" />
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Modal>
    );
  }

  const onMenuClick = (e) => {
    if (e.key === "1") showLoginModal();
    if (e.key === "2") showRegisterModal();
  };

  return (
    <Layout>
      <Header
        style={{
          display: "flex",
          alignItems: "center",
        }}
      >
        <NavLink to={"/dashboard"}>
          <h3 style={{ color: "white" }}>{constants.name}</h3>
        </NavLink>

        <Menu
          onClick={onMenuClick}
          theme="dark"
          mode="horizontal"
          defaultSelectedKeys={["2"]}
          items={items}
          style={{
            flex: 1,
            minWidth: 0,
            justifyContent: "flex-end",
          }}
        />
      </Header>
      <Content
        style={{
          padding: "48px",
        }}
      >
        <div
          style={{
            background: colorBgContainer,
            minHeight: 280,
            padding: 24,
            borderRadius: borderRadiusLG,
          }}
        >
          <h3>An A.I Powered Inventory Management and Point of Sale System</h3>
        </div>
      </Content>
      <Footer
        style={{
          textAlign: "center",
        }}
      >
        Ant Design ©2023 Created by Ant UED
      </Footer>
      <LoginModal />
      <RegisterModal />
    </Layout>
  );
};
export default Landing;
